@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bodoni+Moda:wght@400;600&family=Playfair+Display:wght@400;500&family=Roboto+Condensed:wght@300;400&family=Vidaloka&display=swap");

* {
  margin: 0;
  padding: 0;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 15px;
  color: #343a40;
}
.article {
  padding-inline-start: 0em;
  /* border: 1px solid red; */
}

.logo {
  font-family: "Bodoni Moda", serif;
  font-size: 11em;
  font-weight: 400;
  color: #000;
  letter-spacing: -0.32em;
  /* line-height: -em; */
  z-index: 10;
  margin-left: 180px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  /* border: 1px solid purple; */
}
