} */

.title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 0.5em;
  color: #000;
  /* border: 1px solid yellow; */
}

.category-container {
  display: flex;
  /* border: 1px solid green; */
  margin: 50px 0;
  padding: 1em 300px 1em 300px;
}

.category {
  display: flex;
  flex-direction: column;
  padding: 20px 200px;
  /* border: 1px solid orange; */
  letter-spacing: 1px;
  line-height: 20px;
}

.big-product-container {
  display: flex;
  justify-content: center;
  margin: 120px 0;
  /* border: 1px solid red; */
  flex-wrap: wrap;
  width: 100%;
}

.big-product {
  display: flex;
  flex-direction: column;
  width: 30%;
  /* border: 1px solid green; */
}

.big-product-detail {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto;
  margin-top: 0.6em;
  /* border: 1px solid purple; */
  justify-content: space-between;
}

