.wrapper {
  display: flex;
  border: 1px solid green;
}

.four-column {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  padding: 100px 200px;
  border: 1px solid brown;
  width: 100%;
}

