.small-product {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 320px;
  margin: 0px 10px;
  padding: 0px 0px;
  border: 1px solid blue;
}

.small-product-detail {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto;
  margin-top: 0.6em;
  justify-content: space-between;
}
